import { ChangeEvent, useState } from "react";
import { IKeyword } from "../infra/LocalDB";

type KeywordsProps = {
  categoryId: number; // 空配列を入れたいときもあるのでカテゴリIDは別途必要
  keywords: IKeyword[];
  emitKeywordsChange: (id: number, keywords: string[]) => void;
};

const CategoryKeywordsTextArea = ({
  categoryId,
  keywords,
  emitKeywordsChange,
}: KeywordsProps) => {
  // コンポーネント内では文字列配列として保持する
  const [keywordList, setKeywordList] = useState(keywords.map((it) => it.word));

  const handleTextareaChange = (event: ChangeEvent<HTMLTextAreaElement>) => {
    const newValue = event.target.value;
    // 空文字自体もキーワードとしては保存する。そうしないとTextareaの次の行に行けない
    const newKeywordList = newValue.split("\n");
    setKeywordList(newKeywordList);
    // 親コンポーネントに更新を通知
    emitKeywordsChange(categoryId, newKeywordList);
  };

  return (
    <textarea value={keywordList.join("\n")} onChange={handleTextareaChange} />
  );
};

export default CategoryKeywordsTextArea;
