import { ICategory } from "../infra/LocalDB";

export function getCategoryName(categories: ICategory[], categoryId: number) {
  return categories?.find((elem) => elem.id === categoryId)?.name ?? "その他";
}

export function getCategoryColorCode(
  categories: ICategory[],
  categoryId: number
) {
  return categories?.find((elem) => elem.id === categoryId)?.colorCode ?? null;
}
