import { PieChart, Pie, Cell, Tooltip } from "recharts";
import { CategorySummaryForChart } from "../model/Report";

/*
const data01 = [
  { name: "開発", value: 400 },
  { name: "テスト", value: 300 },
  { name: "MTG", value: 300 },
  { name: "休憩", value: 200 },
];
*/

type PieChartViewProps = {
  summaryList: CategorySummaryForChart[];
};

const OTHERS_COLOR = "#9932CC";

const PieChartView = ({ summaryList }: PieChartViewProps) => {
  const getColor = (index: number): string => {
    // カテゴリに設定されている色を選ぶ
    // 設定がなかったらその他の色にする
    return summaryList[index].colorCode ?? OTHERS_COLOR;
  };

  const renderCustomizedLabel = ({
    cx,
    cy,
    midAngle,
    innerRadius,
    outerRadius,
    index,
    percent,
  }: any) => {
    const RADIAN = Math.PI / 180;
    const radius = 25 + innerRadius + (outerRadius - innerRadius);
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
      <text
        x={x}
        y={y}
        fill={getColor(index)}
        textAnchor={x > cx ? "start" : "end"}
        dominantBaseline="central"
        fontSize={12}
      >
        {summaryList[index].name} ({(percent * 100).toFixed(0) + "%"})
      </text>
    );
  };

  return (
    <PieChart width={350} height={350}>
      <Pie
        dataKey="value"
        isAnimationActive={false}
        data={summaryList}
        cx="50%"
        cy="50%"
        outerRadius={80}
        fill="#8884d8"
        label={renderCustomizedLabel}
      >
        {summaryList.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={getColor(index)} />
        ))}
      </Pie>
      <Tooltip />
    </PieChart>
  );
};

export default PieChartView;
