import React, { useState, ChangeEvent } from "react";
import { ICategory } from "../infra/LocalDB";

type CategoryProps = {
  category: ICategory;
  emitNameChange: (id: number, name: string) => void;
};

const CategoryTextArea = ({ category, emitNameChange }: CategoryProps) => {
  const [categoryName, setCategoryName] = useState(category.name);

  const handleTextareaChange = (event: ChangeEvent<HTMLTextAreaElement>) => {
    const newValue = event.target.value;
    setCategoryName(newValue);
    // 親コンポーネントに更新を通知
    emitNameChange(category.id!, newValue);
  };

  return <textarea value={categoryName} onChange={handleTextareaChange} />;
};

export default CategoryTextArea;
