import React, { useState } from "react";
import { ColorChangeHandler, GithubPicker } from "react-color";
import { ICategory } from "../infra/LocalDB";

const DEFAULT_COLORS = [
  "#0088FE",
  "#00C49F",
  "#FFBB28",
  "#FF8042",
  "#FF6347",
  "#90EE90",
  "#FF4500",
  "#40E0D0",
  "#4169E1",
  "#FFB6C1",
  "#800080",
  "#DAA520",
  "#2E8B57",
  "#C0C0C0",
  "#556B2F",
  "#FFA07A",
];

type CategoryColorPickerProps = {
  category: ICategory;
  emitColorChange: (id: number, color: string) => void;
};

const CategoryColorPicker = ({
  category,
  emitColorChange,
}: CategoryColorPickerProps) => {
  const [colorPickerVisible, setColorPickerVisible] = useState(false);
  const [color, setColor] = useState(category.colorCode);

  const handleColorChangeComplete: ColorChangeHandler = (newColor) => {
    console.log(newColor);
    const newColorCode = newColor.hex;
    setColor(newColorCode);
    emitColorChange(category.id!, newColorCode);

    // 確定なのでピッカーを閉じる
    toggleColorPicker();
  };

  const toggleColorPicker = () => {
    setColorPickerVisible(!colorPickerVisible);
  };

  const buttonStyle = {
    background: color,
  };

  return (
    <div>
      <button style={buttonStyle} onClick={toggleColorPicker}>
        Color
      </button>
      {colorPickerVisible && (
        <GithubPicker
          color={color}
          colors={DEFAULT_COLORS}
          onChangeComplete={handleColorChangeComplete}
        />
      )}
    </div>
  );
};

export default CategoryColorPicker;
