import { ICategory, IKeyword } from "../infra/LocalDB";
import { parse } from "../lib/NoteParser";
import PieChartView from "./PieChartView";
import { CategorySummaryForChart } from "../model/Report";
import { getCategoryColorCode, getCategoryName } from "../lib/CategoryUtils";

type ReportViewProps = {
  value: string;
  keywords: IKeyword[];
  categories: ICategory[];
};

// TODO: PieChartにカテゴリ設定した色を反映させる <イマココ>

// TODO: Weeklyのコンポーネントを汎用として、value一個バージョンをdailyとして共通化したい

const ReportView = ({ value, keywords, categories }: ReportViewProps) => {
  if (!keywords) {
    return <div>NO REPORT</div>;
  }
  const results = parse(value, keywords);
  const totalProcessedTime = results.reduce(
    (acc, cur) => acc + cur.processedTime,
    0
  );

  const summaryList: CategorySummaryForChart[] = results
    .sort((a, b) => a.categoryId - b.categoryId)
    .map((it) => {
      return {
        name: getCategoryName(categories, it.categoryId),
        colorCode: getCategoryColorCode(categories, it.categoryId),
        value: it.processedTime / 60,
      };
    });

  return (
    <div>
      <p>【DAILY】</p>
      {summaryList.map((it) => (
        <div>
          {it.name}[{it.value.toFixed(1)}h]
        </div>
      ))}
      <p>Total {(totalProcessedTime / 60).toFixed(1)}h</p>

      <PieChartView summaryList={summaryList} />
    </div>
  );
};

export default ReportView;
