import React, { useState, useEffect, useCallback, useRef } from "react";
import { useLiveQuery } from "dexie-react-hooks";
import CategoryTextArea from "./CategoryTextArea";
import CategoryKeywordsTextArea from "./CategoryKeywordsTextArea";
import { resetDatabase, addEmptyCategory } from "../infra/LocalDB";
import MyHeader from "./Header";
import db, { deleteCategory } from "../infra/LocalDB";
import CategoryColorPicker from "./CategoryColorPicker";

type NameChangeEvent = {
  categoryId: number;
  name: string;
};

type KeywordsChangeEvent = {
  categoryId: number;
  keywords: string[];
};

type ColorChangeEvent = {
  categoryId: number;
  colorCode: string;
};

const CategoryEditor = () => {
  const categories = useLiveQuery(() => db.categories.toArray());
  const keywords = useLiveQuery(() => db.keywords.toArray());

  // 子コンポーネントでの変更はイベントを渡して、この親コンポーネントで統括処理する(主にDB保存)
  const [nameChangeEvents, setNameChangeEvents] = useState<NameChangeEvent[]>(
    []
  );
  const [keywordsChangeEvents, setKeywordsChangeEvents] = useState<
    KeywordsChangeEvent[]
  >([]);

  const [colorChangeEvents, setColorChangeEvents] = useState<
    ColorChangeEvent[]
  >([]);

  const emitNameChange = useCallback((id: number, newName: string) => {
    const e: NameChangeEvent = { categoryId: id, name: newName };
    // イベントリストにキューイングする
    setNameChangeEvents((prev) => [...prev, e]);
  }, []);

  const emitKeywordsChange = useCallback(
    (id: number, newKeywords: string[]) => {
      const e: KeywordsChangeEvent = { categoryId: id, keywords: newKeywords };
      setKeywordsChangeEvents((prev) => [...prev, e]);
    },
    []
  );

  const emitColorChange = useCallback((id: number, newColor: string) => {
    const e: ColorChangeEvent = { categoryId: id, colorCode: newColor };
    setColorChangeEvents((prev) => [...prev, e]);
  }, []);

  const timerRef = useRef<number>();

  /**
   * 全体保存処理.
   * @param nameChangeEvents
   * @param keywordsChangeEvents
   * @param colorChangeEvents
   */
  const saveChanges = (
    nameChangeEvents: NameChangeEvent[],
    keywordsChangeEvents: KeywordsChangeEvent[],
    colorChangeEvents: ColorChangeEvent[]
  ) => {
    // カテゴリ名保存
    if (nameChangeEvents.length !== 0) {
      const latestNameByCategoryId = new Map<number, string>();
      nameChangeEvents.forEach((ev) => {
        latestNameByCategoryId.set(ev.categoryId, ev.name);
      });
      setNameChangeEvents([]); //clear

      latestNameByCategoryId.forEach((value, key) => {
        // JSのforEachはvalueが先にくる
        db.categories.update(key, { name: value });
      });
      console.log("Save name");
    }

    // キーワード保存
    if (keywordsChangeEvents.length !== 0) {
      const latestKeywordsByCategoryId = new Map<number, string[]>();
      keywordsChangeEvents.forEach((ev) => {
        latestKeywordsByCategoryId.set(ev.categoryId, ev.keywords);
      });
      setKeywordsChangeEvents([]); //clear

      latestKeywordsByCategoryId.forEach((value, key) => {
        const cid = key;
        db.keywords.where("categoryId").equals(cid).delete();
        if (value.length !== 0) {
          // 文字列の配列をキーワードレコードに対応するオブジェクトに変換してbulkAdd
          db.keywords.bulkAdd(
            value.map((it) => ({ categoryId: cid, word: it }))
          );
        }
      });
      console.log("Save keywords");
    }

    // カラー保存
    if (colorChangeEvents.length !== 0) {
      const latestColorByCategoryId = new Map<number, string>();
      colorChangeEvents.forEach((ev) => {
        latestColorByCategoryId.set(ev.categoryId, ev.colorCode);
      });
      setColorChangeEvents([]); //clear

      latestColorByCategoryId.forEach((value, key) => {
        db.categories.update(key, { colorCode: value });
      });
      console.log("Save color");
    }
  };

  useEffect(() => {
    // 変更後一定時間経過で保存
    timerRef.current = window.setTimeout(() => {
      saveChanges(nameChangeEvents, keywordsChangeEvents, colorChangeEvents);

      // Eventsを空にした瞬間も要素変動なのでcallbackは2回動く
      console.log("Save all (auto)");
    }, 2000);

    return () => {
      clearTimeout(timerRef.current);
    };
  }, [nameChangeEvents, keywordsChangeEvents, colorChangeEvents]);

  useEffect(() => {
    // Ctrl+sのイベントを上書き
    const handleKeyDown = (event: KeyboardEvent) => {
      if (
        (event.ctrlKey && event.key === "s") ||
        (event.metaKey && event.key === "s")
      ) {
        event.preventDefault(); // デフォルトの保存動作をキャンセルする
        saveChanges(nameChangeEvents, keywordsChangeEvents, colorChangeEvents);
        console.log("Save all (manual)");
      }
    };

    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  });

  if (!categories) return null;
  if (!keywords) return null;

  return (
    <div>
      <MyHeader />
      <h1>Category Editor</h1>
      <button onClick={resetDatabase}>RESET DB</button>

      {categories.map((it) => (
        <div key={it.id}>
          <label>Category:</label>
          <CategoryTextArea category={it} emitNameChange={emitNameChange} />
          <label>Keywords:</label>
          <CategoryKeywordsTextArea
            categoryId={it.id!}
            keywords={keywords.filter((item) => item.categoryId === it.id)}
            emitKeywordsChange={emitKeywordsChange}
          />
          <CategoryColorPicker
            category={it}
            emitColorChange={emitColorChange}
          />
          <button onClick={() => deleteCategory(it.id!)}>DELETE</button>
        </div>
      ))}

      <button onClick={addEmptyCategory}>ADD</button>
    </div>
  );
};

export default CategoryEditor;
